"use client";

import React, { useEffect, useState } from "react";
import MapComponent from "./MapComponent";

interface User {
  fullName: string;
  mobile: string;
  city: string;
  location: { lat: number; lng: number };
  contacts: { name: string; phone: string }[];
}

interface LoanApplication {
  id: string;
  user: User;
  status: "Applied" | "Pending" | "Approved" | "Rejected";
  rejectionReason?: string;
  isDeleted?: boolean;
}

export default function AdminPage() {
  const [loanApplications, setLoanApplications] = useState<LoanApplication[]>([]);
  const [filterCity, setFilterCity] = useState<string>("");
  const [selectedLocation, setSelectedLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [showMap, setShowMap] = useState(false);
  const [showContacts, setShowContacts] = useState(false);
  const [selectedContacts, setSelectedContacts] = useState<{ name: string; phone: string }[]>([]);
  const [rejectingId, setRejectingId] = useState<string | null>(null);
  const [rejectionReason, setRejectionReason] = useState<string>("");

  useEffect(() => {
    // Mock data for loan applications
    const mockData: LoanApplication[] = [
      {
        id: "1",
        user: {
          fullName: "John Doe",
          mobile: "1234567890",
          city: "Chandigarh",
          location: { lat: 30.7333, lng: 76.7794 },
          contacts: [
            { name: "Jane Doe", phone: "9876543210" },
            { name: "Bob Smith", phone: "5555555555" },
          ],
        },
        status: "Applied",
      },
      {
        id: "2",
        user: {
          fullName: "Alice Johnson",
          mobile: "2345678901",
          city: "Delhi",
          location: { lat: 28.7041, lng: 77.1025 },
          contacts: [{ name: "Charlie Brown", phone: "4444444444" }],
        },
        status: "Rejected",
        rejectionReason: "Incomplete documents",
      },
      {
        id: "3",
        user: {
          fullName: "Michael Smith",
          mobile: "3456789012",
          city: "Mumbai",
          location: { lat: 19.076, lng: 72.8777 },
          contacts: [],
        },
        status: "Approved",
      },
    ];
    setLoanApplications(mockData);
  }, []);

  const filteredApplications = filterCity
    ? loanApplications.filter(
        (app) => app.user.city.toLowerCase() === filterCity.toLowerCase() && !app.isDeleted
      )
    : loanApplications.filter((app) => !app.isDeleted);

  const handleApprove = (id: string) => {
    setLoanApplications((prev) =>
      prev.map((app) => (app.id === id ? { ...app, status: "Approved", rejectionReason: undefined } : app))
    );
  };

  const handleReject = (id: string) => {
    if (!rejectionReason.trim()) {
      alert("Please enter a rejection reason (max 200 characters).");
      return;
    }
    setLoanApplications((prev) =>
      prev.map((app) =>
        app.id === id ? { ...app, status: "Rejected", rejectionReason: rejectionReason.trim() } : app
      )
    );
    setRejectingId(null);
    setRejectionReason("");
  };

  const handleDelete = (id: string) => {
    if (confirm("Are you sure you want to delete this loan application?")) {
      setLoanApplications((prev) =>
        prev.map((app) => (app.id === id ? { ...app, isDeleted: true } : app))
      );
    }
  };

  const openMap = (location: { lat: number; lng: number }) => {
    setSelectedLocation(location);
    setShowMap(true);
  };

  const closeMap = () => {
    setShowMap(false);
    setSelectedLocation(null);
  };

  const openContacts = (contacts: { name: string; phone: string }[]) => {
    setSelectedContacts(contacts);
    setShowContacts(true);
  };

  const closeContacts = () => {
    setShowContacts(false);
    setSelectedContacts([]);
  };

  return (
    <div className="min-h-screen p-6 bg-white">
      <h1 className="text-3xl font-bold mb-6 text-center">Admin Panel - Loan Applications</h1>

      <div className="mb-4 flex justify-center">
        <select
          value={filterCity}
          onChange={(e) => setFilterCity(e.target.value)}
          className="border border-gray-300 rounded px-3 py-2"
        >
          <option value="">Filter by City</option>
          {[...new Set(loanApplications.map((app) => app.user.city))].map((city) => (
            <option key={city} value={city}>
              {city}
            </option>
          ))}
        </select>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full border border-gray-300">
          <thead className="bg-gray-100">
            <tr>
              <th className="border border-gray-300 px-4 py-2">Name</th>
              <th className="border border-gray-300 px-4 py-2">Mobile</th>
              <th className="border border-gray-300 px-4 py-2">City</th>
              <th className="border border-gray-300 px-4 py-2">Status</th>
              <th className="border border-gray-300 px-4 py-2">Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredApplications.length === 0 && (
              <tr>
                <td colSpan={5} className="text-center py-4">
                  No loan applications found.
                </td>
              </tr>
            )}
            {filteredApplications.map((app) => (
              <tr key={app.id} className="hover:bg-gray-50">
                <td className="border border-gray-300 px-4 py-2">{app.user.fullName}</td>
                <td className="border border-gray-300 px-4 py-2">{app.user.mobile}</td>
                <td className="border border-gray-300 px-4 py-2">{app.user.city}</td>
                <td className="border border-gray-300 px-4 py-2">
                  <span
                    className={
                      app.status === "Rejected"
                        ? "text-red-600 font-semibold"
                        : app.status === "Approved"
                        ? "text-green-600 font-semibold"
                        : "text-yellow-600 font-semibold"
                    }
                  >
                    {app.status}
                  </span>
                  {app.status === "Rejected" && app.rejectionReason && (
                    <p className="text-red-600 text-sm mt-1">Reason: {app.rejectionReason}</p>
                  )}
                </td>
                <td className="border border-gray-300 px-4 py-2 space-x-2">
                  <button
                    onClick={() => handleApprove(app.id)}
                    className="bg-green-600 text-white px-3 py-1 rounded hover:bg-green-700"
                    disabled={app.status === "Approved"}
                  >
                    Approve
                  </button>
                  {rejectingId === app.id ? (
                    <>
                      <input
                        type="text"
                        maxLength={200}
                        placeholder="Rejection reason"
                        value={rejectionReason}
                        onChange={(e) => setRejectionReason(e.target.value)}
                        className="border border-gray-300 rounded px-2 py-1 mr-2"
                      />
                      <button
                        onClick={() => handleReject(app.id)}
                        className="bg-red-600 text-white px-3 py-1 rounded hover:bg-red-700"
                      >
                        Submit
                      </button>
                      <button
                        onClick={() => {
                          setRejectingId(null);
                          setRejectionReason("");
                        }}
                        className="bg-gray-400 text-white px-3 py-1 rounded hover:bg-gray-500"
                      >
                        Cancel
                      </button>
                    </>
                  ) : (
                    <button
                      onClick={() => setRejectingId(app.id)}
                      className="bg-red-600 text-white px-3 py-1 rounded hover:bg-red-700"
                      disabled={app.status === "Rejected"}
                    >
                      Reject
                    </button>
                  )}
                  <button
                    onClick={() => handleDelete(app.id)}
                    className="bg-gray-600 text-white px-3 py-1 rounded hover:bg-gray-700"
                  >
                    Delete
                  </button>
                  <button
                    onClick={() => openMap(app.user.location)}
                    className="bg-blue-600 text-white px-3 py-1 rounded hover:bg-blue-700"
                  >
                    View Location
                  </button>
                  <button
                    onClick={() => openContacts(app.user.contacts)}
                    className="bg-indigo-600 text-white px-3 py-1 rounded hover:bg-indigo-700"
                  >
                    View Contacts
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Map Modal */}
      {showMap && selectedLocation && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded p-4 max-w-lg w-full relative">
            <button
              onClick={closeMap}
              className="absolute top-2 right-2 text-gray-600 hover:text-gray-900 font-bold"
            >
              &times;
            </button>
            <h2 className="text-xl font-semibold mb-4">User Location</h2>
            <MapComponent lat={selectedLocation.lat} lng={selectedLocation.lng} />
          </div>
        </div>
      )}

      {/* Contacts Modal */}
      {showContacts && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded p-4 max-w-md w-full relative">
            <button
              onClick={closeContacts}
              className="absolute top-2 right-2 text-gray-600 hover:text-gray-900 font-bold"
            >
              &times;
            </button>
            <h2 className="text-xl font-semibold mb-4">User Contacts</h2>
            {selectedContacts.length === 0 ? (
              <p>No contacts available.</p>
            ) : (
              <ul className="list-disc list-inside space-y-2">
                {selectedContacts.map((contact, index) => (
                  <li key={index}>
                    <span className="font-semibold">{contact.name}</span>: {contact.phone}
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
